"use client";

import axios, { AxiosError } from "axios";
import { resolve } from "path";

type ActionResponse = {
    message:string,
    errors:Array<{ name?:string, email?:string , password?:string} >
}

export const registerAction = async ( prev:ActionResponse, formData:FormData ):Promise<ActionResponse>=>{




    try{
        const res = await axios.post(
            `${process.env.BACKEND_API_URL}/user/register`, 
            { name: formData.get("username"), email: formData.get("email"), password: formData.get("password"), confirm_password: formData.get("confirm-password") }, 
            { headers:{ "Content-Type":"application/json"}}
        );

        console.log(res.data);
       
    }
    catch(err:any){
        console.log(err)

       if( err instanceof AxiosError){
          if(err.status===422){

            console.log(err)
            return { 
                message:"validation Error",
                errors: [],
            }
          }
       }

       return{ 
        message:"Something went wrong", 
        errors:[]
        }
    }



} 





















